<template>
  <div>
    <v-toolbar flat>
      <v-toolbar-title class="mr-6">
        <span>{{ toolbarTitle }}</span>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="mdi-sync"
          :btnTooltip="refreshToolTipTitle"
          buttonSize="medium"
          :onClick="() => refreshGrid()"
        >
        </tooltip-icon-button
      ></v-toolbar-title>
      <grid-items-counter :num="selectedItems.length"></grid-items-counter>
      <header-selector
        v-if="isHeaderConfigurable"
        :grid-config-change="fetchSetGridHeaders"
        :fieldKeys="fieldKeys"
        :tableName="gridName"
      ></header-selector>
      <v-btn v-if="isCreatable" x-small class="mr-2 btn-height-22 white-text" color="green" @click="onCreateDialogOpen">
        <span>Create</span>
        <v-icon x-small right> mdi-plus</v-icon>
      </v-btn>
      <v-btn
        x-small
        color="error"
        @click="onDeleteSelected"
        v-if="isDeletable"
        :disabled="!atleastOneGridItemSelected"
        class="mr-2 btn-height-22 white-text"
      >
        <span>Delete</span>
        <v-icon x-small right> mdi-delete</v-icon>
      </v-btn>
      <v-text-field
        v-model="searchString"
        @input="onSearchChange"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
      <slot
        name="toolbarButtons"
        :refreshGrid="refreshGrid"
        :selectedItems="selectedItems"
        :clearSelectedItems="clearSelectedItems"
      ></slot>
    </v-toolbar>
    <v-data-table
      dense
      show-select
      :show-expand="showExpand"
      :items="items"
      :expanded.sync="expanded"
      :search="searchString"
      :single-expand="singleExpand"
      :item-key="defaultItemKey"
      :headers="gridHeaders"
      v-model="selectedItems"
      :loading="isFetchingItems"
      :item-class="changeItemRowBackground"
      :custom-filter="filterCriteria"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:item.name="{ item }">
        <slot v-if="navigableName" name="routeLinkName" :item="item"></slot>
        <span v-else>{{ item.name }}</span>
      </template>
      <template v-slot:item.priority="{ item }">
        <span>{{ priorityArray(item.priority) }}</span>
      </template>
      <template v-slot:item.gridItemActions="{ item }">
        <slot name="gridItemActions" :item="item" :refreshGrid="refreshGrid"> </slot>
      </template>
      <template v-slot:item.gridItemActionsTwo="{ item }">
        <slot name="gridItemActionsTwo" :item="item" :refreshGrid="refreshGrid"> </slot>
      </template>
      <template v-slot:item.data-table-expand="{ item, expand, isExpanded }">
        <slot name="expansionIcon" :expand="expand" :isExpanded="isExpanded" :item="item"></slot>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <slot name="expansionView" :headers="headers" :item="item"></slot>
        </td>
      </template>
      <template v-if="!hideActions" v-slot:item.actions="{ item }">
        <tooltip-icon-button
          v-if="!!onEdit"
          btnTooltip="Edit"
          btnIcon="mdi-pencil"
          btnColor="blue darken"
          :onClick="() => toggleDialog(dialogNames.editDialog, item)"
        ></tooltip-icon-button>
        <tooltip-icon-button
          v-if="!!onRemove"
          btnTooltip="Delete"
          btnIcon="mdi-delete"
          btnColor="error darken"
          :onClick="() => toggleDialog(dialogNames.removeDialog, item)"
        ></tooltip-icon-button>
        <slot name="afterActions" :item="{ item }" :refreshGrid="refreshGrid"> </slot>
      </template>
    </v-data-table>
    <confirm-dialog
      :loading="isDeletingItem"
      dialog-text="Are you sure?"
      :is-open="dialog[dialogNames.removeDialog]"
      :on-close="() => closeDialog(dialogNames.removeDialog)"
      :on-confirm="() => onRemove()"
    ></confirm-dialog>
    <confirm-dialog
      :loading="isDeletingItem"
      :dialog-text="deleteAllTitle"
      :is-open="dialog[dialogNames.removeAllDialog]"
      :on-close="() => closeDialog(dialogNames.removeAllDialog)"
      :on-confirm="() => onRemoveAll()"
    ></confirm-dialog>
    <v-dialog
      v-model="dialog[dialogNames.createDialog]"
      persistent
      :fullscreen="isFullScreen"
      :max-width="dialogMaxWidth"
    >
      <slot
        name="createForm"
        :on-save="onCreate"
        :is-saving="isCreatingItem"
        :is-open="dialog[dialogNames.createDialog]"
        :on-close="() => closeDialog(dialogNames.createDialog)"
      ></slot>
    </v-dialog>
    <v-dialog
      v-model="dialog[dialogNames.editDialog]"
      :fullscreen="isFullScreen"
      persistent
      :max-width="dialogMaxWidth"
    >
      <slot
        name="editForm"
        :is-edit="true"
        :on-save="onEdit"
        :item="selectedItem"
        :is-saving="isEditingItem"
        :is-open="dialog[dialogNames.editDialog]"
        :on-close="() => closeDialog(dialogNames.editDialog)"
      ></slot>
    </v-dialog>
  </div>
</template>
<script>
import ConfirmDialog from './ConfirmDialog.vue';
import GridItemsCounter from './gridItemsCounter.vue';
import * as gridNames from '../../constants/gridNames';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import HeaderSelector from '../HeaderSelector/headerSelector.vue';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import * as headerBuilderService from '../../services/headerBuilderService';
import * as gridTableHeadersService from '../../services/gridTableHeadersService';

const dialogNames = {
  editDialog: 'editDialog',
  removeDialog: 'removeDialog',
  createDialog: 'createDialog',
  removeAllDialog: 'removeAllDialog',
};
const customFieldRenderer = {
  priorityArray: priorityArray => {
    delete priorityArray.point_uuid;
    return Object.keys(priorityArray).reduce((acc, curr, index, self) => {
      const currentVal = priorityArray[curr];
      if (currentVal) {
        acc += `${curr}: ${currentVal}${index !== self.length - 1 ? ', ' : ''}`;
      }
      return acc;
    }, '');
  },
};
export default {
  name: 'grid',
  props: [
    'headers',
    'showExpand',
    'service',
    'toolbarTitle',
    'itemKey',
    'storeMutationKey',
    'singleExpand',
    'params',
    'getDataCb',
    'creatable',
    'deletable',
    'gridName',
    'hideActions',
    'navigableName',
    'isHeaderConfigurable',
    'isCreateAllowableFn',
    'itemRowBackground',
    'extraConfig',
    'isFullScreen',
  ],
  components: { ConfirmDialog, HeaderSelector, TooltipIconButton, GridItemsCounter },
  data() {
    return {
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.editDialog]: false,
        [dialogNames.removeDialog]: false,
        [dialogNames.createDialog]: false,
        [dialogNames.removeAllDialog]: false,
      },
      expanded: [],
      selectedItems: [],
      searchString: '',
      search: [],
      isFetchingItems: false,
      isCreatingItem: false,
      isEditingItem: false,
      isDeletingItem: false,
      isRemovingSelected: false,
      items: [],
      selectedItem: null,
      tableProps: { ...DATA_TABLE_PROPS },
      queryOrConfig: {},
      newHeaders: [],
      fieldKeys: [],
    };
  },
  mounted() {
    if (this.params && Object.keys(this.params).length > 0) {
      this.queryOrConfig = { ...this.queryOrConfig, ...this.params };
    }
    this.fetch();
    this.fetchSetGridHeaders();
  },
  watch: {
    params: function (currVal, prevVal) {
      if (this.params && currVal != prevVal) {
        this.queryOrConfig = {
          ...this.queryOrConfig,
          ...this.params,
        };
      }
    },
  },
  computed: {
    defaultItemKey() {
      if (!this.itemKey) {
        return 'uuid';
      }
      return this.itemKey;
    },
    dialogMaxWidth() {
      return this.isFullScreen ? '' : '500px';
    },
    gridHeaders() {
      let headers = [];
      if (
        this.newHeaders &&
        this.newHeaders.length !== 0 &&
        this.gridName &&
        this.isHeaderConfigurable &&
        this.fieldKeys.length > 0
      ) {
        headers = this.newHeaders;
      } else {
        headers = this.headers;
      }
      return headers;
    },
    atleastOneGridItemSelected() {
      return this.selectedItems.length > 0;
    },
    deleteAllTitle() {
      return `Are you sure to delete ${this.selectedItems.length} item(s)?`;
    },
    refreshToolTipTitle() {
      return `Refresh ${this.toolbarTitle}`;
    },
    isCreatable() {
      return typeof this.creatable === 'undefined' ? true : this.creatable;
    },
    isDeletable() {
      return typeof this.deletable === 'undefined' ? true : this.deletable;
    },
  },
  methods: {
    changeItemRowBackground(item) {
      if (typeof this.itemRowBackground === 'function') {
        return this.itemRowBackground(item);
      }
    },
    refreshGrid() {
      this.fetch();
    },
    toggleDialog(dialogName, item) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    setFieldKeys(dataItems) {
      if (Array.isArray(dataItems) && dataItems.length > 0 && dataItems[0]) {
        this.fieldKeys = Object.keys(dataItems[0]);
      }
    },
    fetch() {
      this.isFetchingItems = true;
      return this.service
        .fetch(this.queryOrConfig)
        .then(data => {
          if (typeof this.getDataCb === 'function') {
            this.getDataCb(data);
          }
          let dataItems = data;
          if (!Array.isArray(data) && data.hasOwnProperty('dataItems')) {
            dataItems = data.dataItems || [];
          }
          this.setFieldKeys(dataItems);
          if (this.storeMutationKey) {
            this.$store.commit(this.storeMutationKey, dataItems);
          }
          this.items = dataItems.map(item => ({
            ...item,
            isExapanded: false,
          }));
          return dataItems;
        })
        .catch(err => {
          this.$toasted.show('Error fetching grid items', { type: 'error' });
          return err;
        })
        .finally(() => {
          this.isFetchingItems = false;
        });
    },
    onEdit(data) {
      this.isEditingItem = true;
      return this.service
        .patch(this.selectedItem.uuid, data, this.queryOrConfig)
        .then(data => {
          this.fetch();
          return data;
        })
        .finally(() => {
          this.isEditingItem = false;
        });
    },
    onCreate(data) {
      this.isCreatingItem = true;
      return this.service
        .create(data, this.queryOrConfig)
        .then(data => {
          this.fetch();
          return data;
        })
        .finally(() => {
          this.isCreatingItem = false;
          this.selectedItems = [];
        });
    },
    onRemove() {
      this.isDeletingItem = true;
      return this.service
        .removeById(this.selectedItem.uuid, this.queryOrConfig)
        .then(data => {
          this.fetch();
          this.$toasted.show('Deletion successful');
          this.closeDialog(this.dialogNames.removeDialog);
          return data;
        })
        .finally(() => {
          this.isDeletingItem = false;
          this.selectedItems = [];
        });
    },
    fetchSetGridHeaders() {
      if (!this.gridName || !this.isHeaderConfigurable) {
        this.newHeaders = this.headers;
        return;
      }
      gridTableHeadersService
        .fetch(this.gridName)
        .then(gridConfig => {
          const gridHeaders = headerBuilderService.gridHeadersResolver(this.gridName, gridConfig);
          this.newHeaders = headerBuilderService.buildHeaders(this.extraConfig)(gridHeaders);
        })
        .catch(err => {
          this.newHeaders = this.headers || gridNames.DEFAULT_HEADERS[this.gridName];
        });
    },
    onCreateDialogOpen() {
      let allow = true;
      if (typeof this.isCreateAllowableFn === 'function') {
        allow = this.isCreateAllowableFn();
      }
      if (allow) {
        this.toggleDialog(this.dialogNames.createDialog);
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedApp = {};
    },
    onDeleteSelected() {
      this.toggleDialog(this.dialogNames.removeAllDialog);
    },
    onSearchChange() {
      this.search = this.searchString.split(',');
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.search &&
        Array.isArray(this.search) &&
        this.search.length > 0 &&
        this.search.every(val => typeof val === 'string') &&
        this.search.some(
          searchVal => value.toString().toLocaleLowerCase().indexOf(searchVal.toLocaleLowerCase()) !== -1,
        )
      );
    },
    clearSelectedItems() {
      this.selectedItems = [];
    },
    onRemoveAll() {
      this.isRemovingSelected = true;
      return Promise.all(this.selectedItems.map(item => this.service.removeById(item.uuid, this.queryOrConfig)))
        .then(data => {
          this.$toasted.show('Deletion of selected item(s) successful');
          this.fetch();
          this.closeDialog(this.dialogNames.removeAllDialog);
          return data;
        })
        .catch(() => {
          this.$toasted.show('Deletion unsucessful', { type: 'error' });
        })
        .finally(() => {
          this.selectedItems = [];
        });
    },
    ...customFieldRenderer,
  },
};
</script>
<style>
.background-yellow {
  background: #fbb93e;
}
</style>
